/*animation circle page connexion */
.circle1{
    background-color: rgba(184, 50, 162,.2);
    position:absolute;
    width:1700px;
    height:1700px;
    border-radius:100%;
    top:-500px;
    left:-1000px;
    -webkit-animation:  linear 20s slidein infinite;
            animation:  linear 20s slidein infinite;
    z-index:0;
}
.circle2{
    background-color: rgba(184, 50, 162,.1);
    position:absolute;
    width:1700px;
    height:1700px;
    border-radius:100%;
    top:-500px;
    right:-1000px;
    -webkit-animation:  linear 20s slidein infinite;
            animation:  linear 20s slidein infinite;
    z-index:0;
}
.circle3{
    background-color: rgba(129, 126, 129, 0.2);
    position:absolute;
    width:1700px;
    height:1700px;
    border-radius:100%;
    top:-1300px;
    right:0;
    -webkit-animation:  linear 20s slidein2 infinite;
            animation:  linear 20s slidein2 infinite;
    z-index:0;
}

@-webkit-keyframes slidein {
   0%{
  -webkit-transform:scale(1);
          transform:scale(1)
    }
  
    50% {
     -webkit-transform:scale(.5);
             transform:scale(.5)
    }
    100%{
     -webkit-transform:scale(1);
             transform:scale(1)
    }
  }

@keyframes slidein {
   0%{
  -webkit-transform:scale(1);
          transform:scale(1)
    }
  
    50% {
     -webkit-transform:scale(.5);
             transform:scale(.5)
    }
    100%{
     -webkit-transform:scale(1);
             transform:scale(1)
    }
  }
  @-webkit-keyframes slidein2 {
    0%{
   -webkit-transform:scale(1);
           transform:scale(1)
     }
   
     50% {
      -webkit-transform:scale(.8);
              transform:scale(.8)
     }
     100%{
      -webkit-transform:scale(1);
              transform:scale(1)
     }
   }
  @keyframes slidein2 {
    0%{
   -webkit-transform:scale(1);
           transform:scale(1)
     }
   
     50% {
      -webkit-transform:scale(.8);
              transform:scale(.8)
     }
     100%{
      -webkit-transform:scale(1);
              transform:scale(1)
     }
   }
